@import '../../global.scss';

.companyContainer {
    position: relative;
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    background: white;
    color: black;
    font-family: 'Inter', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    .companyContent {
        position: relative;
        z-index: 1;
        padding-bottom: 100px;
    }
}

.heroSection {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 1;
    display: flex;
    align-items: center;
}

.heroBackground {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../../assets/Company/Company.jpg') no-repeat center center;
    background-size: cover;
    background-position: 60%;
    filter: brightness(0.6) contrast(1.2);
    z-index: 1;
}

.heroOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom,
            rgba(0, 0, 0, 0.3) 0%,
            rgba(0, 0, 0, 0.5) 50%,
            rgba(0, 0, 0, 0.9) 100%);
    z-index: 2;
}

.heroContent {
    position: relative;
    max-width: 1400px;
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
    align-items: center;
    height: 100%;
    padding: 120px 2rem 0;
    z-index: 3;
    box-sizing: border-box;

    @media screen and (max-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 1rem;
        gap: 2rem;
        height: 100%;
        width: 100%;
        margin: 0;
    }
}

.heroLeft {
    @media screen and (max-width: 768px) {
        width: 100%;
        max-width: 100%;
        padding: 0;
        margin: 0;
    }

    h1 {
        font-size: 1.5rem;
        font-weight: 500;
        color: white;
        margin-bottom: 1rem;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        word-wrap: break-word;
        overflow-wrap: break-word;

        @media screen and (max-width: 768px) {
            font-size: 1.125rem;
            margin-bottom: 0.75rem;
            text-align: center;
            width: 100%;
        }
    }

    h2 {
        font-size: 4rem;
        font-weight: 700;
        line-height: 1.1;
        color: white;
        margin-bottom: 3rem;
        letter-spacing: -0.02em;
        word-wrap: break-word;
        overflow-wrap: break-word;

        @media screen and (max-width: 768px) {
            font-size: 2.25rem;
            margin-bottom: 1.5rem;
            line-height: 1.2;
            text-align: center;
            width: 100%;
        }
    }

    .heroStats {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
        margin-top: 3rem;

        @media screen and (max-width: 1200px) {
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
        }

        @media screen and (max-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;
            margin-top: 2rem;
            padding: 0;

            .stat {
                width: 100%;
                max-width: 220px;
                text-align: center;
            }
        }

        .stat {
            @media screen and (max-width: 768px) {
                text-align: center;
                width: 100%;
            }

            .statValue {
                display: block;
                font-size: 2.5rem;
                font-weight: 700;
                color: white;
                margin-bottom: 0.5rem;
                word-wrap: break-word;
                overflow-wrap: break-word;

                @media screen and (max-width: 768px) {
                    font-size: 2rem;
                    margin-bottom: 0.25rem;
                }
            }

            .statLabel {
                font-size: 1rem;
                color: white;
                font-weight: 500;
                word-wrap: break-word;
                overflow-wrap: break-word;

                @media screen and (max-width: 768px) {
                    font-size: 0.875rem;
                }
            }
        }
    }
}

.heroRight {
    @media screen and (max-width: 768px) {
        width: 100%;
        max-width: 100%;
        padding: 0;
        margin: 0;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .lead {
        font-size: 1.25rem;
        line-height: 1.7;
        color: white;
        margin-bottom: 2rem;
        word-wrap: break-word;
        overflow-wrap: break-word;

        @media screen and (max-width: 768px) {
            font-size: 1.125rem;
            line-height: 1.6;
            margin-bottom: 1.5rem;
            width: 100%;
            text-align: center;
        }
    }

    .ctaButton {
        background: #0066FF;
        color: white;
        border: none;
        padding: 1rem 2rem;
        font-size: 1.125rem;
        font-weight: 500;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        @media screen and (max-width: 768px) {
            width: 100%;
            max-width: 300px;
            margin: 0 auto;
            padding: 1rem;
            font-size: 1.125rem;
        }

        &:hover {
            background: #0052cc;
        }
    }
}

.contentSection {
    position: relative;
    width: 100%;
    background: white;
    padding: 120px 0;
    z-index: 2;
}

.contentWrapper {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 2rem;
    box-sizing: border-box;

    @media screen and (max-width: 768px) {
        padding: 0 1rem;
    }
}

.sectionHeader {
    text-align: center;
    max-width: 800px;
    margin: 0 auto 4rem;
    padding: 0 1rem;
    box-sizing: border-box;

    @media screen and (max-width: 768px) {
        margin-bottom: 3rem;
    }

    h3 {
        font-size: 2.5rem;
        font-weight: 700;
        margin-bottom: 1rem;
        color: #1a1a1a;
        word-wrap: break-word;
        overflow-wrap: break-word;
    }

    .sectionSubtitle {
        font-size: 1.25rem;
        line-height: 1.6;
        color: #4a4a4a;
        word-wrap: break-word;
        overflow-wrap: break-word;
    }
}

.capabilityGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    box-sizing: border-box;

    @media screen and (max-width: 1200px) {
        grid-template-columns: repeat(2, 1fr);
        max-width: 800px;
        gap: 2rem;
    }

    @media screen and (max-width: 768px) {
        grid-template-columns: 1fr;
        gap: 1.5rem;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.capabilityBlock {
    background: #f8f9fa;
    border-radius: 12px;
    padding: 2rem;
    transition: transform 0.3s ease;
    width: 100%;
    box-sizing: border-box;

    @media screen and (max-width: 768px) {
        padding: 1.5rem;
        text-align: center;
        max-width: 400px;
        width: 100%;
        margin: 0 auto;
    }

    &:hover {
        transform: translateY(-5px);
    }

    .capabilityIcon {
        width: 48px;
        height: 48px;
        margin-bottom: 1.5rem;

        @media screen and (max-width: 768px) {
            width: 40px;
            height: 40px;
            margin: 0 auto 1rem;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .capabilityContent {
        h4 {
            font-size: 1.5rem;
            font-weight: 700;
            margin-bottom: 1rem;
            color: #1a1a1a;
            word-wrap: break-word;
            overflow-wrap: break-word;

            @media screen and (max-width: 768px) {
                font-size: 1.25rem;
                margin-bottom: 0.75rem;
            }
        }

        p {
            font-size: 1rem;
            line-height: 1.6;
            color: #4a4a4a;
            word-wrap: break-word;
            overflow-wrap: break-word;

            @media screen and (max-width: 768px) {
                font-size: 1rem;
                line-height: 1.5;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .heroContent {
        gap: 40px;
    }

    .heroLeft h2 {
        font-size: 3rem;
    }

    .capabilityGrid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 768px) {
    .heroContent {
        grid-template-columns: 1fr;
        padding: 80px 1rem 0;
        gap: 2rem;
        height: auto;
        min-height: 100vh;
    }

    .heroLeft {
        h1 {
            font-size: 1.125rem;
            margin-bottom: 0.75rem;
            text-align: center;
        }

        h2 {
            font-size: 2.25rem;
            margin-bottom: 1.5rem;
            line-height: 1.2;
            text-align: center;
        }

        .heroStats {
            grid-template-columns: repeat(2, 1fr);
            gap: 1.5rem;
            margin-top: 2rem;
            padding: 0 1rem;

            .stat {
                text-align: center;

                .statValue {
                    font-size: 2rem;
                    margin-bottom: 0.25rem;
                }

                .statLabel {
                    font-size: 0.875rem;
                }
            }
        }
    }

    .heroRight {
        text-align: center;
        padding: 0 1rem;

        .lead {
            font-size: 1.125rem;
            line-height: 1.6;
            margin-bottom: 1.5rem;
        }

        .ctaButton {
            width: 100%;
            max-width: 300px;
            margin: 0 auto;
            padding: 1rem;
            font-size: 1.125rem;
        }
    }

    .contentSection {
        padding: 60px 0;
    }

    .contentWrapper {
        padding: 0 1rem;
    }

    .sectionHeader {
        padding: 0 1rem;
        margin-bottom: 3rem;

        h3 {
            font-size: 1.75rem;
            margin-bottom: 0.75rem;
        }

        .sectionSubtitle {
            font-size: 1.125rem;
            line-height: 1.5;
        }
    }

    .capabilityGrid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
        padding: 0 0.5rem;
    }

    .capabilityBlock {
        padding: 1.5rem;
        text-align: center;

        .capabilityIcon {
            width: 40px;
            height: 40px;
            margin: 0 auto 1rem;
        }

        .capabilityContent {
            h4 {
                font-size: 1.25rem;
                margin-bottom: 0.75rem;
            }

            p {
                font-size: 1rem;
                line-height: 1.5;
            }
        }
    }
}